.social__list {
  @include list-reset;
  display: flex;
  justify-content: center;
  gap: 24px;
}

.social__list-link {
  &::before {
    content: "";
    display: block;
    width: 28px;
    height: 22px;
    background: $color-social-link-clickable;
  }

  &:focus::before {
    background: $color-social-link-hover;
  }

  &:hover::before {
    background: $color-social-link-hover;
  }

  &:active {
    opacity: 0.3;
  }

  &--vk {
    mask: url("../img/vk-icon.svg") no-repeat center / contain;
  }

  &--youtube {
    mask: url("../img/youtube-icon.svg") no-repeat center / contain;
  }

  &--telegram {
    mask: url("../img/telegram-icon.svg") no-repeat center / contain;
  }
}
